const HelperTools = ({ open, onClose }) => {

    const [hp1, setHp1] = React.useState(10);
    const [hp2, setHp2] = React.useState(10);

    const [timeStone, setTimeStone] = React.useState(0);

    return (
        <Dialog open={open} onClose={onClose}>
            <DialogTitle>========</DialogTitle>
            <div className="tool-box">

                <span>Player1 Hp</span>
                <DialogTitle>
                    <Slider
                        className="slider-box"
                        aria-label="Temperature"
                        defaultValue={hp1}
                        valueLabelDisplay="auto"
                        shiftStep={10}
                        step={1}
                        marks
                        min={0}
                        max={10}
                        onChange={(event) => {
                            setHp1(event.target.value);
                        }}
                    />
                </DialogTitle>

                <span>Player2 Hp</span>
                <DialogTitle>
                    <Slider
                        className="slider-box"
                        aria-label="Temperature"
                        defaultValue={hp2}
                        valueLabelDisplay="auto"
                        shiftStep={10}
                        step={1}
                        marks
                        min={0}
                        max={10}
                        onChange={(event) => {
                            setHp2(event.target.value);
                        }}
                    />
                </DialogTitle>

                <span></span>
                <DialogTitle>
                    <Slider
                        color='#007bff'
                        aria-label="Temperature"
                        defaultValue={timeStone}
                        valueLabelDisplay="auto"
                        shiftStep={9}
                        step={1}
                        marks
                        min={-4}
                        max={4}
                        onChange={(event) => {
                            setTimeStone(event.target.value);
                        }}
                    />
                </DialogTitle>

            </div>
        </Dialog>
    )
}

